.App {
  text-align: center;
  height: 100%; }

header > a {
  position: absolute;
  right: 2rem;
  top: 1.25rem;
  width: 25px;
}

header > a  > img {
  width: 100%;
}

main {
  height: 100%; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  padding: 1rem;
  color: white; }

.App-title {
  font-size: 1.5em;
  text-align: left;
  padding-left: 5rem; 
  margin: 0;
}

.App-intro {
  font-size: large; }

.grid-row {
  display: -ms-flexbox;
  display: flex;
  height: calc(50vh - 34px); 
}

.grid-row > div {
  -webkit-box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.05);
          box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.05);
  overflow: hidden;
  width: 50%; 
}
.grid-row > div:first-child {
  -ms-flex-align: center;
      align-items: center;
  background-color: transparent;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transition: background-color 0.5s ease-in, color 0.5s ease-in;
  -o-transition: background-color 0.5s ease-in, color 0.5s ease-in;
  transition: background-color 0.5s ease-in, color 0.5s ease-in; 
}
.grid-row > div:first-child:hover {
  background-color: #04DEAD;
  color: #fff; 
}

.grid-row > div span > img {
  height: 94%;
  min-width: 100%;
  left: 50%;
  position: absolute;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);

}

.grid-row > div .LazyLoad.modal-mode > div  span > img {
  opacity: 0;
}


@media (max-width: 576px) {
  .App-title {
    font-size: 0.9rem;
    text-align: left;
    padding-left: 3.5rem;
    margin: 0;
    padding-top: 10px;
  }
  
  .grid-row {
    display: block;
    height: calc(100vh - 68px); 
  }
  .grid-row > div {
    height: 50vh;
    width: 100%; 
  }
  .grid-row > div .LazyLoad > div > span > img {

    -webkit-transform: translateX(-50%);

        -ms-transform: translateX(-50%);

            transform: translateX(-50%); 
    position: absolute;
    left: 50%;
    width: auto;
  }

}


.LazyLoad {
  opacity: 0;
  -webkit-transition: opacity 0.75s ease-in, -webkit-transform 0.25s ease-in;
  transition: opacity 0.75s ease-in, -webkit-transform 0.25s ease-in;
  -o-transition: opacity 0.75s ease-in, transform 0.25s ease-in;
  transition: opacity 0.75s ease-in, transform 0.25s ease-in;
  transition: opacity 0.75s ease-in, transform 0.25s ease-in, -webkit-transform 0.25s ease-in;
  -webkit-transform: translateY(100%);
      -ms-transform: translateY(100%);
          transform: translateY(100%); }
  .LazyLoad.is-visible {
    opacity: 1;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0); 
  }
  .LazyLoad.is-visible.modal-mode {
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
    -webkit-transform: none;
        -ms-transform: none;
            transform: none;
  }

.backdrop {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background-color: rgba(0, 0, 0, 0.75);
  padding: 130px 50px 50px;
  display: -ms-flexbox;
  display: flex;
  z-index: 10000; }

.modal {
  background-color: white;
  height: calc(100vh - 160px);
  min-height: 300px;
  margin: 0px auto;
  padding: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  -ms-flex-line-pack: center;
      align-content: center;
  width: calc(100vw - 190px);
  -ms-flex-pack: center;
      justify-content: center; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }



